<template>
  <div class='auth'>
    <div v-if="formType === 'signIn'">
      <sign-in />
    </div>
    <div v-if="formType === 'signUp'">
      <sign-up v-bind="{toggleForm}" />
    </div>
    <div v-if="formType === 'forgotPassword'">
      <forgot-password v-bind="{toggleForm}" />
    </div>
    <div class="link-container">
      <p v class='toggleButton' v-on:click="toggleForm(formType === 'signIn' ? 'signUp' : 'signIn')">{{ formTitle }}</p>
      <p v-if="formType == 'signIn'" class='toggleButton' v-on:click="toggleForm('forgotPassword')">忘記密碼？</p>
    </div>
  </div>
</template>

<script>
import SignIn from './SignIn.vue'
import SignUp from './SignUp.vue'
import ForgotPassword from './ForgotPassword.vue'

export default {
  name: 'auth',
  data() {
    return {
      formType: 'signIn',
      formTitle: '還沒註冊？'
    }
  },
  components: {
    SignIn,
    SignUp,
    ForgotPassword
  },
  methods: {
    toggleForm(formType) {
      this.formType = formType
      if (formType === 'signIn') this.formTitle = '還沒註冊？'
      if (formType === 'signUp') this.formTitle = '已經有一個帳號？'
    }
  }
}
</script>

<style scoped>
.link-container {
  margin-top: 30;
}
.auth {
  display: flex;
  flex-direction: column;
}
.toggleButton {
  cursor: pointer;
  margin-bottom: 0;
}
.toggleButton:hover {
  opacity: .7;
}
</style>
